.message {
    margin-bottom: 30px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    background-color: #06C;
    filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));
}

.imageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 48px;
}

.text {
    width: 522px;
    color: #000;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
}

.text:after {

}

.name {
    text-align: right;
    font-weight: 600;
}

.messageText {
    text-align: right;
    font-weight: 400;
}

.time {
    text-align: right;
    color: #000;
    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
    font-weight: 600;
}