.friendMessage {
    margin-bottom: 30px;
}

.friendImageAndText {
    display: flex;
    flex-direction: row;
    background-color: #F5F7FB;
    filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 48px;
}

.friendText {
    width: 522px;
    color: #000;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
}

.friendText:after {

}

.friendName {
    font-weight: 600;
}

.friendMessageText {
    font-weight: 400;
}

.friendTime {
    color: #000;
    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
    font-weight: 600;
}
